import React from "react";
import "./Dashboard2.css";
import Footer from "../Footer/Footer";
import { Link } from "react-router-dom";
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import TwitterIcon from "@mui/icons-material/Twitter";
const Dashboard = ({ setActive, active }) => {
  return (
    <div className="dashboard">
      <div className="host__raffle__dashboard">
        <h2>Host a Raffle</h2>
        <Link to="/host-raffle/create">
          <h6
            onClick={() => setActive("create")}
            className={active === "create" && "active"}
          >
            Create New{" "}
          </h6>
        </Link>
        <Link to="/host-raffle/live">
          <h6
            onClick={() => setActive("live")}
            className={active === "live" && "active"}
          >
            Live
          </h6>
        </Link>
        <Link to="/host-raffle/pending">
          <h6
            onClick={() => setActive("pending")}
            className={active === "pending" && "active"}
          >
            Pending
          </h6>
        </Link>
        <Link to="/host-raffle/ended">
          <h6
            onClick={() => setActive("ended")}
            className={active === "ended" && "active"}
          >
            Ended
          </h6>
        </Link>
      </div>
      <div className="dashboard__footer">
        <div className="nav__links">
          <Link to="/" onClick={() => console.log("clicked")}>
            Home
          </Link>
          <Link to="/search-raffles">Win Prizes</Link>
          <Link to="/account">My Account</Link>
          {/* <Link to="/about">About</Link>
          <Link to="/contact-us">Contact Us</Link>
          <Link to="/faqs">FAQs</Link> */}
        </div>
        <div className="social__links">
          <Link to="www.facebook.com">
            <FacebookIcon />
          </Link>
          <Link to="www.instagram.com">
            <InstagramIcon />
          </Link>
          <Link to="www.linkedin.com">
            <LinkedInIcon />
          </Link>
          <Link to="www.twitter.com">
            <TwitterIcon />
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
